@import "./css-variables.scss";

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  background-color: var(--color-grey1);
}

// GENERAL START
.pass-left-border {
  border-left-color: var(--color-pass);
}
.fail-left-border {
  border-left-color: var(--color-fail);
}
.pass-color {
  color: var(--color-pass);
}
.fail-color {
  color: var(--color-fail);
}
.primary-color {
  color: var(--color-primary);
}
.grey-color-6 {
  color: var(--color-grey6);
}
.pass-light-bgcolor {
  background-color: var(--color-pass-light);
}
.fail-light-bgcolor {
  background-color: var(--color-fail-light);
}
// GENERAL END

// NAVBAR STYLE START
.navbar {
  /* adding line-height for nav makes 
  the Export option render badly */
  /* line-height: 53px;*/
  height: 53px;
  background-color: #ffffff;
  padding: 10px 15px;
  width: 100%;

  .nav-left-logo {
    // color: #0066b3;
    .nav-left-product-name {
      color: var(--color-primary);
      font-weight: bold;
      font-size: 1.3em;
      margin-left: 10px;
      vertical-align: sub;
      display: inline-block;
    }
  }

  .nav-right-name {
    float: right;
    // color: var(--color-primary);
    /* font-size: 16px; */
    // font-weight: bold;

    /* style for export button(both) */
    .exp-btn {
      color: var(--color-primary);
      font-weight: 500;
    }
    .exp-btn:focus {
      // font-weight: 700;
      // color: black;
      background-color: var(--color-grey1);
    }
  }

  /* override the padding for the popover content; used for: export options popover, test info icon hover */
  .ant-popover-inner-content {
    padding: 0;
  }

  .popover-content {
    padding: 12px 16px;
    width: 14.6em;

    .choose-file-msg {
      margin: 0;
      color: var(--color-grey2);
    }

    /* applies to export file format Dropdown in export option popover*/
    .exp-options-sel-dropdown.ant-select:not(.ant-select-customize-input)
      .ant-select-selector {
      border-radius: 0;
      background-color: var(--color-grey3); //#e6e6e6;
      border-bottom: 2px solid black;
    }

    .exp-options-sel-dropdown.ant-select {
      /* Fix size if you dont want the popover size to change based on the info icon show/hide */
      /* width: 12.3em; */
      width: 100%;
    }

    .exp-set-pref-lbl {
      margin: 20px 0 0 0;
      color: var(--color-grey2);
    }

    .info-icon-btn {
      border: none;
      margin-left: 5px;
      padding: 0;
      height: 1px;
    }

    .ico-style {
      color: var(--color-primary);
    }

    /* style for export button inside export option popover*/
    .exp-btn-in-popover {
      background-color: var(--color-primary);
      color: white;
      width: 100%;
      margin: 20px 0 0 0;
      border-radius: 0;
    }
    .exp-btn-in-popover:focus {
      border: 2px solid #40a9ff;
    }
  }
}
// NAVBAR STYLE END

// TOP STATUS FRAGEMNT STYLE START
.top-name-status-section {
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 5px;
  margin-top: 5px;
  padding: 5px;
  border-bottom: 1px solid lightgrey;

  .top-name-status-section-icon {
    margin-right: 10px;
    color: var(--color-grey4);
  }

  .ico-svg {
    font-size: 20px;
    vertical-align: middle;
    color: var(--color-grey4);
  }

  .ico-btn {
    padding: 0px;
    border-radius: 50px;
    border-color: transparent;
    background-color: transparent;
  }

  .top-name-status-section-testname {
    height: 18px;
    font-size: 18px;
    margin-bottom: 5px;
    margin-top: 5px;
    color: var(--color-grey4);
  }

  .top-auto-heal {
    height: 18px;
    width: 53px;
    border-radius: 14px;
    color: black;
    background-color: lightgray;
    font-family: Roboto, sans-serif;
    font-size: 12px;
    line-height: 14px;
    padding: 4px 12px;
    margin-left: 10px;
    letter-spacing: 2px;
    vertical-align: text-top;
  }

  .top-status-flag {
    height: 18px;
    width: 53px;
    border-radius: 14px;
    color: #ffffff;
    font-family: Roboto, sans-serif;
    font-size: 12px;
    line-height: 14px;
    padding: 4px 12px;
    margin-left: 10px;
    letter-spacing: 2px;
    vertical-align: text-top;
  }

  .top-status-flag-abort-icon {
    color: var(--color-grey4);
    font-size: 1.2em;
    margin-left: 0.4em;
    vertical-align: sub;
  }

  .top-status-loadiing-icon {
    margin-left: 10px;
    font-size: 25px;
  }

  .top-status-time {
    color: var(--color-grey4);
    font-size: 14px;
    line-height: 16px;
  }

  .top-name-status-line2 {
    height: 16px;
    font-size: 14px;
    margin-bottom: 10px;
  }
}

// TOP STATUS FRAGEMNT STYLE END

// SUMMARY CHART SECTION START
/* maintaining margin for each section instead of full row */
.summary-chart-section {
  margin-left: 15px;
  margin-right: 7px;
  margin-top: 5px;
  min-height: 348px;
  height: 35vh;
  background-color: #ffffff;
  /* box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1); */
  box-shadow: 0 2px 4px 0 rgba(199, 199, 199, 0.5);

  .summary-chart-sec-details-div {
    font-size: 16px;
    color: #555;
    padding: 15px 10px;
    border-bottom: 1px solid var(--color-grey5);
  }

  .summary-chart-sec-detail-right-div {
    float: right;
    margin-right: 10px;
  }

  .summary-chart-sec-doughnut-div {
    width: 80%;
    margin: 10px;
  }
}

// MODAL START(PART OF SUMMARY TABS)
.aut-modal {
  border-top: 4px solid var(--color-primary);
  border-radius: 0px;

  .aut-items {
    margin: auto;
    padding: 1%;
    height: 10%;
    width: 100%;
    list-style-type: none;
    display: flex;
    padding-left: 4%;
  }

  .footer-btn {
    height: 40px;
    width: 75px;
    background-color: var(--color-primary);
    border-radius: 0px;
  }
}
// MODAL END(PART OF SUMMARY TABS)

// SUMMARY CHART SECTION END

// SUMMARY TABS SECTION START

.summary-tabs-section {
  margin-right: 15px;
  margin-left: 7px;
  margin-top: 5px;
  height: 35vh;
  min-height: 348px;
  background-color: #ffffff;
  /* box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1); */
  box-shadow: 0 2px 4px 0 rgba(199, 199, 199, 0.5);
  /* border: 1px solid green; */

  .summary-tabs-ul {
    margin: auto;
    padding: 0px;
  }

  .summary-tabs-ul-li {
    margin: auto;
    padding: 1%;
    height: 10%;
    width: 100%;
    list-style-type: none;
    display: flex;
    padding-left: 4%;
  }

  .summary-tabs-ul-li-key {
    width: 22%;
    color: var(--color-grey4);
    font-size: 14px;
    font-weight: bold;
    left: 3%;
    margin-left: 2%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /* position: absolute; */
    /* display: inline-block; */
  }

  .summary-tabs-ul-li-ellipsis {
    width: 70%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .summary-tabs-ul-li-icon {
    position: relative;
    font-size: 18px;
    vertical-align: middle;
  }

  .summary-tabs-tests-lbl {
    margin-left: 40px;
  }

  .summary-test-counters1 {
    box-sizing: border-box;
    height: 20%;
    border: 1px solid #e4e1e1;
    border-radius: 3px;
    margin-left: 30px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px 15px 5px 15px;
    position: relative;
    left: 1%;
    float: left;
  }

  .summary-test-counter-val {
    font-size: 20px;
    font-weight: bold;
  }

  .app-runtime-span {
    margin-right: 15px;
  }

  .app-runtime-span-badge {
    .ant-badge-count {
      right: -2px;
      font-size: 10px;
      background-color: #fff;
      color: #999;
      box-shadow: 0 0 0 1px #d9d9d9 inset;
    }
  }

  .app-runtime-span-btnimg {
    padding: 0px;
    border-radius: 50px;
    border-color: transparent;
    background-color: transparent;
  }

  .app-runtime-span-img {
    border-radius: 50%;
  }

  // START NEW ANTD 4.X Tabs-------
  // adjust height of tabs to match with left sec
  .ant-tabs-nav {
    height: 53px;
  }

  // for summary tab
  .summ-tab {
    margin: 0 35px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }

  // for resource tab
  .res-tab {
    margin: 0 35px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }

  // style the active tab
  .ant-tabs-tab-active {
    font-weight: 500;
    .summ-tab {
      color: black;
      text-transform: uppercase;
      letter-spacing: 0.05em;
    }

    .res-tab {
      color: black;
      text-transform: uppercase;
      letter-spacing: 0.05em;
    }
  }

  // No extra margin for subsequent tab after 1st tab
  .ant-tabs-tab + .ant-tabs-tab {
    margin: 0;
  }

  // style the bar for selected tab
  .ant-tabs-ink-bar {
    background: var(--color-primary) !important;
  }

  // Tabs component contect section margin
  .ant-tabs-top > .ant-tabs-nav,
  .ant-tabs-bottom > .ant-tabs-nav,
  .ant-tabs-top > div > .ant-tabs-nav,
  .ant-tabs-bottom > div > .ant-tabs-nav {
    margin: 0 0 5px 0;
  }
  // END NEW ANTD 4.X Tabs---------
}

// applies to aut images in tabs as well as when Modal is opened; hence outside
.aut-logo-img-modal-also {
  width: 20px;
  height: inherit;
  border-radius: 9px;
  vertical-align: middle;
  margin-right: 5px;
}

/* applies when tab key is used to focus summary tabs */
body .p-tabview.p-tabview-top .p-tabview-nav li a:not(.p-disabled):focus,
body .p-tabview.p-tabview-bottom .p-tabview-nav li a:not(.p-disabled):focus,
body .p-tabview.p-tabview-left .p-tabview-nav li a:not(.p-disabled):focus,
body .p-tabview.p-tabview-right .p-tabview-nav li a:not(.p-disabled):focus {
  box-shadow: 0 0 0 0.02em var(--color-primary);
}

/* override test tab back ground color etc */
.ant-collapse > .ant-collapse-item {
  /* border-bottom: 5px solid rgb(231, 229, 229); */
  background-color: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(199, 199, 199, 0.6);
}

/* overrides the color of active test tab  */
.ant-collapse > .ant-collapse-item-active {
  background-color: rgba(0, 102, 179, 0.06);
}

