// TEST DETAILS SECTION START

/* applies to filter select position */
.filter-main-div {
  height: 35px;
  margin: 15px 15px 5px 15px;

  /* applies to the text placed at the same level as filter */
  .test-details-text {
    font-size: 18px;
    font-weight: bold;
  }

  /* applies to the filter string */
  .filter-text-and-select-container {
    display: flex;
    float: right;
  }
  .filter-text-and-select-container > .filter-text {
    margin: 2% 5px 0 0;
    // flex:1 should be ideally present, but removed for IE
    // as IE doesn't support. We can continue to use this
    // if required and ignore IE support
    // flex: 1;
  }

  /* applies to filter component */
  .filter-selection-component {
    width: 200px;
    border: 1px solid lightgray;
    border-radius: 0;
  }
}

// should be outside any other class as its a popup kind
.filter-chk-title {
  margin-left: 5px;
}

/* applies to whole collapse compoent */
.test-tab-collpase {
  margin: 0 15px 0 15px;
  background-color: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(199, 199, 199, 0.5);

  .collapse-panel {
    border-bottom: 5px solid #e7e5e5;
    border-left-width: 5px;
    border-left-style: solid;
  }

  .test-type-ico-btn {
    padding: 0px;
    border-radius: 50px;
    border-color: transparent;
    background-color: transparent;
    vertical-align: -webkit-baseline-middle;
  }

  .test-type-icon {
    font-size: 18px;
    vertical-align: bottom;
  }

  .test-tab-name {
    vertical-align: middle;
  }

  // applies to position of arrow icon for test tab
  .anticon.anticon-right.ant-collapse-arrow {
    vertical-align: sub;
  }

  .test-tab-header {
    vertical-align: -webkit-baseline-middle;
  }

  .failed-test-steps-count-icon {
    font-size: 20px;
    margin-right: 5px;
    vertical-align: middle;
  }

  .fail-tests-or-steps-ico-btn {
    // padding: "0px",
    border-radius: 50px;
    border-color: transparent;
    background-color: transparent;
    vertical-align: -webkit-baseline-middle;
  }

  .runtime-img-btn {
    padding: 0px;
    border-radius: 50px;
    border-color: transparent;
    background-color: transparent;

    .img {
      border-radius: 50%;
    }
  }
}

/* makes the test's tab bg-color when on focus using keyboard tab */
.ant-collapse > .ant-collapse-item > .ant-collapse-header:focus {
  background-color: rgba(0, 102, 179, 0.06);
  border: 1px solid rgba(0, 102, 179, 0.06);
}

// TEST DETAILS SECTION END

/* modal style for for both when carousel is opened and when inside test tab, image is clicked to view full */
.modal-carousel-or-imgzoom {
  border-top: 4px solid var(--color-primary);
  border-radius: 0;
  top: 10px;

  .modal-event-title {
    background-color: #f4f4f4;
    margin-bottom: 5px;
    border-left-width: 5px;
    border-left-style: solid;
  }

  .carousel-img {
    margin-left: auto;
    margin-right: auto;
    width: auto;
    height: 75vh;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    border: 1px solid lightgray;
  }
  .carousel-header {
    padding-left: 5px;
    background-color: #f4f4f4;
    margin-bottom: 5px;
    border-left-width: 5px;
    border-left-style: solid;
  }
}

// TEST TAB CONTENTS SECTION START

/* overrides the height of the test tab contents */
.ant-collapse-content > .ant-collapse-content-box {
  padding: 7px;
}

.test-tab-contents {
  height: 46vh;
  display: flex;
  justify-items: flex-start;

  /* applies to step details table row */
  .details-table-row {
    line-height: 14px;
  }

  .tree-col {
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
    background-color: var(--color-grey1);
    resize: horizontal;
    //
    width: 33%;
    min-width: 33%;
    max-width: 50%;
  }

  .tree-col:hover {
    overflow: scroll;
  }

  .tree-col::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  // This css class is a copy of "tree-col" class.
  // This is achhieve a diff behaviour of hover conditionally
  .tree-col-execution-not-finished {
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
    background-color: var(--color-grey1);
    resize: horizontal;
    width: 33%;
    min-width: 33%;
    max-width: 50%;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #ccc;
  }

  /* For var substitution in WebUI */
  subst {
    background-color: rgb(240, 230, 240);
    color: rgb(100, 0, 150);
  }

  .steps-tree-comp {
    background-color: #f2f2f2;
    min-height: 350px;
    padding: 5px 0px 0px 0px;
  }

  .step-info-section {
    width: 67%;
    min-width: 50%;
    height: inherit;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .divider-stepdesc-navs {
    height: 2em;
  }

  .divider-stepdesc-table {
    margin: 0 0 15px 0;
  }

  .divider-stepstbl-scrshot {
    height: 33vh;
    margin-left: 15px;
  }

  .steps-tree-node {
    width: "-webkit-fill-available";
  }

  // Applies to scrollbar for  selected step details table
  .ant-table-body::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  .nav-btn-pareent-span {
    float: right;
    margin-right: 10px;
    font-size: 25px;

    .test-tab-content-nav-btn {
      border: none;
      font-size: 23px;

      .nav-btn-icon {
        font-size: inherit;
        vertical-align: baseline;
      }
    }
  }

  .test-tab-content-snapshot-in-tab-noimg {
    height: 30vh;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .test-tab-content-snapshot-in-tab {
    margin-top: 15px;
    margin-right: auto;
    margin-left: auto;
    max-height: 23vh;
    height: auto;
    width: auto;
    object-fit: fit;
    display: flex;
    justify-content: center;
    max-width: 95%;
    border: 1px solid lightgray;
  }

  .test-tab-content-button-for-snapshot-in-tab {
    margin: 0 auto;
    padding: 0;
    border-color: transparent;
    background-color: transparent;
    display: flex;
    justify-content: center;
  }

  .step-desc-n-status-row {
    display: flex;
    // justify-content: flex-start;

    .step-status {
      min-width: fit-content;
      margin-left: 2%;
      margin-right: 2%;
      // flex: 0 0 6%;
    }

    .step-status-icon {
      font-size: 20px;
      vertical-align: sub;
    }

    .test-tab-content-desc-sec-status-str {
      height: 19px;
      width: 37.87px;
      font-size: 16px;
      font-weight: bold;
      line-height: 19px;
      margin-left: 10px;
    }
  }

  .desc-sec-parent-div {
    width: 85%;
    flex: 1;
    .test-tab-content-desc-sec-desc {
      color: var(--color-grey4);
      font-size: 16px;
      // font-weight: bold;
      font-weight: 500;
      line-height: 19px;

      .desc-expand-text {
        font-size: 12px;
      }
    }
  }

  .below-desc-alert {
    font-size: 13px;
    padding: 0px 0px 0px 15px;
    border: none;
    margin: 5px 0px 3px 15px;
    width: 100%;

    .alert-expand-text {
      font-size: 10px;
    }
  }

  .vp-tree-types-table-row {
    vertical-align: top;
  }

  /* applies to te "Expand" text for overflow description and error */
  .ant-typography a {
    color: var(--color-primary);
  }

  /* Applies to iteration details table */
  .itr-details-table {
    margin-left: 15px;
  }

  .iter-summ-container {
    margin-left: 15px;

    .iter-summ-heading {
      font-weight: bold;
    }
  }

  /* Applies to iter details like passed, failed etc counters */
  .itr-step-counter-span {
    border: 1px solid #e4e1e1;
    margin: 0px;
    padding: 5px 15px;
    margin-right: 15px;
  }

  /* Applies to iter details counters position */
  .itr-step-counter-container {
    margin-top: 10px;
    display: flex;
    justify-content: flex-start;
  }

  /* Applies to value of iter details like passed, failed etc counters  */
  .itr-step-counter-span > .itr-step-counter-val-span {
    font-weight: bold;
  }

  /* Applies to dataset csv location container */
  .itr-dataset-disp-str-container {
    margin: 25px 0 5px 15px;
  }

  /* Applies to dataset csv location string */
  .itr-dataset-disp-str-container > .itr-dataset-disp-str {
    font-weight: bold;
  }

  /* to make the step title in single line with nowrap */
  .ant-tree .ant-tree-treenode {
    white-space: nowrap;
    height: 38px;
    width: -webkit-fill-available;
  }

  /* applies to tree(test) step selection */
  .ant-tree .ant-tree-node-content-wrapper.ant-tree-node-selected {
    background-color: #fff;
    width: inherit;
  }

  /* applies to container where tree is present */
  .ant-tree-show-line .ant-tree-switcher {
    background: transparent;
  }

  // to adjust the red/green msg under step description in test tab section
  div.ant-typography,
  .ant-typography p {
    margin-bottom: 0em;
  }
}
// Fix for: when test accordion was opened the horizontal
// scroll appeared due to width prop. We dont want 
// horizontal scroll to appear at all. This happened when 
// accordion was opened due to width of treenode
.test-tab-contents .ant-tree .ant-tree-treenode {
  width: unset;
}

.step-snapshot-in-dialog-title {
  padding-left: 5px;
  background-color: #f4f4f4;
  margin-bottom: 5px;
  border-left-width: 5px;
  border-left-style: solid;
}

.testtab-img-in-modal {
  /* margin-left: 5%; */
  max-width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border: 1px solid lightgray;
}

/* applies to antd table row hover; Used for step's info table, diff VP tables to manage background on hover */
.antd-table-row-hover-unset
  > .ant-spin-nested-loading
  > .ant-spin-container
  > div
  > .ant-table-container
  // > .ant-table-scroll
  > .ant-table-body
  > table
  > tbody
  > tr:hover
  > td {
  background-color: unset;
}

// Fixes minor margin allignment issue for Table VP
.ant-table.ant-table-small
  .ant-table-tbody
  .ant-table-wrapper:only-child
  .ant-table {
  margin: 0;
}

