/* applies to steps with tree, the "plus" symbol's color adjustment */
/* for all generic VPs with tree and properties, make the background transparent for caret icon */
.ant-tree.ant-tree-show-line li span.ant-tree-switcher {
  background-color: transparent;
}

/* below code ensures the scroll bar in the table also looks similar
